/* ÂÆûÁé∞ÁöÑÂá†‰∏™ DOM Êé•Âè£
** dom.create(`<div>hi</div>`) // Áî®‰∫éÂàõÂª∫ËäÇÁÇπ
** dom.after(node, node2) // Áî®‰∫éÂêëÂêéËøΩÂä†ÂÖÑÂºüËäÇÁÇπ
// ÂéüÁîüÁöÑÊèê‰æõ‰∫Ü‰∏Ä‰∏™ÂÖºÂÆπÊÄß‰∏ç‰Ω≥ÁöÑÂÆûÈ™åÊÄßÊé•Âè£`ChildNode.after() MDN`
** dom.before(node, node2) // Áî®‰∫éÂêëÂâçËøΩÂä†ÂÖÑÂºüËäÇÁÇπ
** dom.append(parent, child) // Áî®‰∫éÂàõÂª∫Â≠êËäÇÁÇπ
** dom.wrap(`<div></div>`) //  Áî®‰∫éÂàõÂª∫Áà∂ËäÇÁÇπ
*/
// dom.create = function() {}

window.dom = {
    // create: function() {} // ÂèØÁÆÄÂåñ‰∏∫
    create(tagName /* ËØ≠‰πâÂåñ ÂΩ¢ÂèÇ */ ) {
        // return document.createElement(tagName) // ‰∏çËÉΩÂàõÂª∫Â∏¶ÊúâÁªìÊûÑÁöÑ HTML ÂÖÉÁ¥†`<div><span>1</span></div>`

        // const container = document.createElement("div")
        const container = document.createElement("template");
        container.innerHTML = tagName.trim(); // Èô§ÂéªÁ©∫Ê†º
        // return container.children[0]
        return container.content.firstChild;
        /* Â≠òÂú® ‰∏çÂèØËØÜÂà´ÂÖÉÁ¥†(<td></td>)ÁöÑ bug
         ** <td</td>> ‰∏çËÉΩÂçïÁã¨Â≠òÂú® Âè™ËÉΩÊîæÂú®<table></table> Èáå<tr></tr>Êàñ<tbody></tbody> ÈáåÔºåÊîæÂú® div Èáå‰∏çÁ¨¶Âêà HTML ËØ≠Ê≥ï
         ** ÂèØ‰ª•Êîæ‰ªªÊÑèÂÖÉÁ¥†Ôºå‰∏çÂá∫ bug ÁöÑÊ†áÁ≠æÊòØ <template></template>
         ** <template></template> ÊòØÁ™úÈó®Áî®Êù•ÂÆπÁ∫≥‰∫∫‰ª•Ê†áÁ≠æÁöÑ
         ** <template></template> Áî®template.content.firstChildÊãøÂà∞
         */
    },
    after(node, node2) {
        // Âú®ÂêéÈù¢ÊèíÂÖ•ËäÇÁÇπÔºåÂ∞±Áõ∏ÂΩì‰∫éÂú®Ê≠§ node ÂêéÈù¢ÁöÑËäÇÁÇπÁöÑÂâçÈù¢Êèí // ÂøÖÈ°ªË∞ÉÁî®Áà∂ËäÇÁÇπÁöÑ insertBefore() ÊñπÊ≥ï
        console.log(node.siblings); // null ?
        node.parentNode.insertBefore(node2, node.nextSibling);
        /* Âà§Êñ≠ ÊéíÈô§ÊúÄÂêé‰∏Ä‰∏™ËäÇÁÇπ Ê≤°Êúâ‰∏ã‰∏Ä‰∏™ËäÇÁÇπ null ‰πüÁ¨¶Âêà */
    },
    before(node, node2) {
        node.parentNode.insertBefore(node2, node);
    },
    append(parent, node) {
        parent.appendChild(node);
    },
    wrap(node, parent) {
        dom.before(node, parent); // Â∞ÜË¶ÅÂåÖË£πÁöÑ‚ÄúÁà∂ËäÇÁÇπ‚ÄùÂÖàÊèíÂà∞ÁõÆÊ†áËäÇÁÇπÁöÑÂâçÈù¢
        dom.append(parent, node); // ÂÜçÊääÁõÆÊ†áËäÇÁÇπÁî® append ÁßªËá≥Â∞ÜË¶ÅÂåÖË£πÁöÑÁà∂ËäÇÁÇπÁöÑ‰∏ãÈù¢
    },
    remove(node) {
        // node.remove() // IE ‰∏çÊîØÊåÅ ÂÖºÂÆπÊÄß‰∏çÂ•Ω
        node.parentNode.removeChild(node);
        return node; // ‰ªçÁÑ∂ÈúÄË¶ÅËé∑ÂèñÊ≠§ËäÇÁÇπÁöÑÂºïÁî®
    },
    empty(node) {
        // Ê∏ÖÁ©∫ node ÈáåÈù¢ÁöÑÊâÄÊúâÂ≠êÂÖÉÁ¥†
        // node.innerHTML = ''
        // const childNodes = node.childNodes ÂèØ‰ª•ÊîπÂÜôÊàê‰ª•‰∏ãÁöÑÂÜôÊ≥ï
        /*
         ** const {childNodes} = node // Ëß£ÊûÑËµãÂÄº
         */
        const array = [];
        /*
         **    for (let i = 0; i < childNodes.length; i++) { // ‰∏çÈúÄË¶Åi++ÁöÑÂæ™ÁéØÂ∞±Áî® while Âæ™ÁéØ‰ª£Êõø
         **        console.log(childNodes)
         **        console.log(childNodes.length)
         **        dom.remove(childNodes[i]) // remove( nodes) ‰ºöÂÆûÊó∂ÊîπÂèò nodes ÁöÑÈïøÂ∫¶ÊØèÊ¨°Âáè‰∏Ä ÂØºËá¥Âæ™ÁéØÁöÑÈïøÂ∫¶‰∏çÂõ∫ÂÆö Âá∫Áé∞ bug
         **        array.push(childNodes[i])
         **    }
         */
        //  ‰∏çÈúÄË¶Åi++ÁöÑÂæ™ÁéØÂ∞±Áî® while Âæ™ÁéØ‰ª£Êõøfor Âæ™ÁéØ

        /* Ëé∑ÂèñÁ¨¨‰∏Ä‰∏™Â≠êËäÇÁÇπ Âπ∂ push ËøõÊï∞ÁªÑ */
        let x = node.firstChild;
        while (x) {
            // Â¶ÇÊûú x Â≠òÂú®
            array.push(dom.remove(node.firstChild));
            x = node.firstChild; // Á¨¨‰∏Ä‰∏™Â≠êËäÇÁÇπÂ∑≤ÁªèÁßªÈô§ ÂéüÂÖàÁ¨¨‰∫åËäÇÁÇπÂ∞±Âèò‰∏∫Áé∞Âú®ÁöÑÁ¨¨‰∏Ä‰∏™ËäÇÁÇπ
        }
        return array; // ‰ªçÁÑ∂ÈúÄË¶ÅËé∑ÂèñÊ≠§ËäÇÁÇπÁöÑÂºïÁî®
    },
    /* Êîπ Áî®‰∫éËØªÂÜôÂ±ûÊÄß */
    /* Áî®Âà§Êñ≠ arguments ÁöÑ‰∏™Êï∞Êù•ÈáçËΩΩÂáΩÊï∞ */
    /* ÈáçËΩΩ
     ** Êúâ‰∏â‰∏™ÂΩ¢ÂèÇÊó∂ÔºåÂ∞±ÊòØËÆæÁΩÆÔºõ
     ** Á¨¨‰∫å‰∏™ÂΩ¢ÂèÇÊó∂ÔºåÂ∞±ÊòØËØªÂèñ
     */
    attr(node, name, value) {
        // ÁªÑÂêà
        if (arguments.length === 3) {
            node.setAttribute(name, value); // ÂéüÁîüDOM setAttribute(name, value)
        } else if (arguments.length === 2) {
            return node.getAttribute(name); // ÂéüÁîüDOM getAttribute(name) Âπ∂ËøîÂõûÂÄº
        }
    },
    /* Áî®‰∫éËØª/ÂÜôÊñáÊú¨ÂÜÖÂÆπ */
    /* ÈáçËΩΩ
     ** Êúâ‰∏§‰∏™ÂΩ¢ÂèÇÊó∂ÔºåÂ∞±ÊòØËÆæÁΩÆÔºõ
     ** Á¨¨‰∏Ä‰∏™ÂΩ¢ÂèÇÊó∂ÔºåÂ∞±ÊòØËØªÂèñ
     */
    text(node, string) {
        // ËÆæËÆ°Ê®°Âºè ‰πã ÈÄÇÈÖç
        // console.log('innerText' in node) //true
        if (arguments.length === 2) {
            /* ÂÜô */
            if ("innerText" in node) {
                node.innerText = string; // IE // ‰ºöÂ∞ÜËäÇÁÇπÂéüÊú¨ÁöÑÊâÄÊúâÂÜÖÂÆπÔºåÂåÖÊã¨Ê†áÁ≠æÂÖ®ÈÉ®ÊîπÂèò
            } else {
                node.textContent = string; // Chrome/ Firefox // ‰ºöÂ∞ÜËäÇÁÇπÂéüÊú¨ÁöÑÊâÄÊúâÂÜÖÂÆπÔºåÂåÖÊã¨Ê†áÁ≠æÂÖ®ÈÉ®ÊîπÂèò
            }
        } else if (arguments.length === 1) {
            /* ËØª */
            if ("innerText" in node) {
                return node.innerText; // IE // ‰ºöÂ∞ÜËäÇÁÇπÂéüÊú¨ÁöÑÊâÄÊúâÂÜÖÂÆπÔºåÂåÖÊã¨Ê†áÁ≠æÂÖ®ÈÉ®ÊîπÂèò
            } else {
                return node.textContent; // Chrome/ Firefox // ‰ºöÂ∞ÜËäÇÁÇπÂéüÊú¨ÁöÑÊâÄÊúâÂÜÖÂÆπÔºåÂåÖÊã¨Ê†áÁ≠æÂÖ®ÈÉ®ÊîπÂèò
            }
        }
    },
    /* Áî®‰∫éËØª/ÂÜôHTMLÂÜÖÂÆπ */
    html(node, string) {
        if (arguments.length === 2) {
            node.innerHTML = string;
        } else if (arguments.length === 1) {
            return node.innerHTML;
        }
    },
    /* Áî®‰∫é‰øÆÊîπstyle */
    /* ÈáçËΩΩ
     ** Á¨¨‰∫å‰∏™ÂΩ¢ÂèÇÊòØÂØπË±°Êó∂ÔºåÂ∞±ÊòØËÆæÁΩÆÔºõdom.style(div, {color: "red"})
     ** Êúâ‰∏â‰∏™ÂΩ¢ÂèÇÊó∂Ôºå‰πüÊòØËÆæÁΩÆÔºõdom.style(div, 'color', 'red')
     ** Á¨¨‰∫å‰∏™ÂΩ¢ÂèÇÊòØÂ≠óÁ¨¶‰∏≤Êó∂ÔºåÂ∞±ÊòØËØªÂèñ dom.style(div, 'color')
     */
    style(node, name, value) {
        if (arguments.length === 3) {
            // dom.style(div, "color", "red"')
            return node.style[name] = value;
            // node.style.name = value;
        } else if (arguments.length === 2) {
            if (typeof name === "string") {
                // ËØªÂèñ dom.style(div, 'color')
                return node.style[name];
            } else if (name instanceof Object /* true */ ) {
                // dom.style(div, {color:'red'})
                const object = name;
                for (let key in object) {
                    // ÈÅçÂéÜËØªÂèñÊâÄÊúâÂØπÂ∫îÁöÑkey
                    // key: border | color | ¬∑¬∑¬∑
                    // node.style.border = ...
                    // node.style.color = ...
                    // Ë∞ÉÁî®Â±ûÊÄßÂÄº []ÊñπÊ≥ï ËØªÂèñÁöÑÊó∂ÂèòÈáè;ÁÇπÊñπÊ≥ï ËØªÂèñÁöÑÊòØÂ≠óÁ¨¶‰∏≤
                    // node.style.key; // Â≠óÁ¨¶‰∏≤
                    node.style[key] = object[key];
                }
                return object
            }
        }
    },
    class: {
        /* Áî®‰∫éÊ∑ªÂä†class */
        add(node, className) {
            node.classList.add(className)
        },
        /* Áî®‰∫éÂà†Èô§class */
        remove(node, className) {
            node.classList.remove(className)
        },
        has(node, className) {
            return node.classList.contains(className)
        }
    },
    on(node, eventName, fn) {
        node.addEventListener(eventName, fn)
    },
    off(node, eventName, fn) {
        console.log(`${eventName}ÂèñÊ∂à‰∫ã‰ª∂`)
        node.removeEventListener(eventName, fn)
    },
    toggle(node, eventName, fn) {
        node.addEventListener("mousedown", function() {
            console.log("Èº†Ê†áÊåâ‰∏ã‰∫Ü");
            node.addEventListener(eventName, fn);
            node.addEventListener("mouseup", function() {
                console.log("Èº†Ê†áÊä¨Ëµ∑‰∫Ü");
                node.removeEventListener(eventName, fn)
            })
        });
    },
    /* scope ‰∏∫Êü•ÊâæÁöÑËåÉÂõ¥ ËäÇÁÇπÂØπË±° */
    find(selector, scope) {
        /* Â¶ÇÊûúÊúâ scope ËäÇÁÇπ Â∞±Êâæ scope ÈáåÁöÑÔºõÊ≤°ÊúâÂ∞±Êâæ document ÈáåÁöÑ */
        return (scope || document).querySelectorAll(selector)
            /* ËøîÂõûÁöÑÊòØ NodeList ‰º™Êï∞ÁªÑ ÂèñÁî®Âä† NodeList[0] */
    },
    parent(node) {
        return node.parentNode
    },
    children(node) {
        return node.children
    },
    siblings(node) {
        return Array.from(node.parentNode.children)
            .filter(n => n !== node)
    },
    next(node) {
        let x = node.nextSibling
            /* ÊéíÈô§ÊñáÊú¨ËäÇÁÇπ */
        while (x && x.nodeType === 3) {
            x = x.nextSibling
        }
        return x
    },
    previous(node) {
        let x = node.previousSibling
            /* ÊéíÈô§ÊñáÊú¨ËäÇÁÇπ */
        while (x && x.nodeType === 3) {
            x = x.previousSibling
        }
        return x
    },
    each(nodeList, fn) {
        for (let i = 0; i < nodeList.length; i++) {
            fn.call(null, nodeList[i])
        }
    },
    index(node) {
        const list = dom.children(node.parentNode)
        let i
        for (i = 0; i < list.length; i++) {
            if (list[i] === node) {
                break
            }
        }
        return i
    }
};